import React, {useRef, useState} from 'react'
import TypeWriter from "../../utils/typeWritter/TypeWritter";


import './DialogDiv.scss'
import dialogIcon from '../../../../src/assets/png/icons/msg.svg';
import returnNextIcon from "../../../assets/png/icons/nextReturnIcon.svg";
import returnPng from "../../../assets/png/icons/return.svg";
import nextPng from "../../../assets/png/icons/next.svg";
let indexText = {
    count: 0,
    animation: 0
};

const DialogDiv = (props) => {
    const dialogRef = useRef()
    const msg = [
        {
            name: "Unknown",
            text: "  Hi, my name is B-12.",
            animation: 3
        },
        {
            name: "B-12",
            text: "  I don't know where I am, but I know who placed me here. The guy whose name is David.",
            animation: 1
        },
        {
            name: "B-12",
            text: "  I know that he's frontend developer and he's able to do such things as he did to me.",
            animation: 2
        },
        {
            name: "B-12",
            text: "  WOW!",
            animation: 0
        },
        {
            name: "B-12",
            text: "  Sorry...It was unexpected...",
            animation: 3
        },
        {
            name: "B-12",
            text: "  So I know that he can make really good websites and he's passionate with coding",
            animation: 1
        },
        {
            name: "B-12",
            text: "  Please reach out to this guy, via contact details below and tell him to free me! He will let me out of this scary place only if you hire him to work on your next project",
            animation: 2
        }
    ]
    const [text,setText] = useState(msg[0].text)
    const DialogIcons = () => {
        let png;
        if(indexText.count === 6) {
            png = <img src={returnPng} alt="return"/>
        } else {
            png = <img src={nextPng} alt="next"/>
        }
        return (
            <>
                {png}
            </>
        )
    }
    return (
        <div className="dialog__container "
             onClick={(e) =>
             {
                 indexText.count += 1;
                 if (indexText.count >= msg.length) indexText.count = 0
                 indexText.count = indexText.count % msg.length;
                 indexText.animation = msg[indexText.count].animation
                 setText(msg[indexText.count].text)
                 props.Count(indexText)

             }
             }
        >
            <span className="border__both"></span>
            <div className="dialog__content">
                <div className="dialog__border__top">
                    <div className="speaker__name">
                        { msg[indexText.count].name }
                    </div>
                    <img src={dialogIcon} alt="dialog_icon"/>
                </div>
                <div ref={dialogRef} className="speaker__text">
                    <TypeWriter text={text}/>
                </div>
                <div className="dialog_navigation_flex">
                    <DialogIcons />
                    <img src={returnNextIcon} alt=""/>
                </div>
            </div>
        </div>

    )
}

export default DialogDiv