import * as THREE from 'three'
import React, {Suspense, useState, useEffect, useRef } from 'react'
import { a, useTransition } from "@react-spring/web";
import {Canvas, extend, useThree, useFrame } from "react-three-fiber";
import { UnrealBloomPass } from 'three-stdlib'
import {AdaptiveDpr, Html, PerspectiveCamera, useProgress, BakeShadows, Preload} from "@react-three/drei";
import B12 from "../models/B13";
import './Scene.scss'
import DialogDiv from "./DialogDiv";

extend({ UnrealBloomPass })

export default function Scene(props){

    const cameraRef = useRef()

    const [numCount, setCount] = useState({count:0,animation:0})
    const Count = ({count,animation}) => {
        setCount({count:count,animation: animation})
        return {count:count,animation: animation}
    }
    function Loader() {
        const { active, progress } = useProgress();
        const transition = useTransition(active, {
            from: { opacity: 1.6, progress: 0 },
            leave: { opacity: 0 },
            update: { progress },
        });
        return transition(
            ({ progress, opacity }, active) =>
                active && (
                    <a.div className='loading' style={{ opacity }}>
                        <div className='loading-bar-container'>
                            <a.div className='loading-bar' style={{ width: progress }}></a.div>
                        </div>
                    </a.div>
                )
        );
    }





    return (

        <div id="CanvasSection">
            <div className="dialog_container">
                <DialogDiv  Count={Count}/>
            </div>
            <Canvas shadows performance={{ min: 0.5 }}>
                <Html fullscreen className="background_msg_div"  zIndexRange={[-2, -1]}>
                </Html>
                <Suspense fallback={null}>
                    <pointLight position={[-15, 10, 10]} intensity={.6} color={'#d4d480'}/>
                    <B12 numCount={numCount}/>
                    <Preload all />
                    <AdaptiveDpr pixelated />
                    <PerspectiveCamera ref={cameraRef}  makeDefault={true} far={1000} near={.9} fov={44.1} position={[0, 0, 0]} rotation={[0, 0, 0]} />
                </Suspense>
            </Canvas>
            <Loader />
        </div>
    )
}
