import React, { useRef, useLayoutEffect, useEffect } from 'react'
import './Skills.scss'
import Robotscene from '../../three/models/Robotscene'
import zurk_data from "../../utils/zurksGenerator/zurk_data";
import skillbar_data from './skillbar_data';
import nftExampleSite from '../../../assets/png/library/nftSite.png'
import emptyExample from '../../../assets/png/library/Empty.png'
const library_database = [
    {
        name: "nftSite",
        url: 'https://davidlichenko.github.io/example_site_1/',
        image: nftExampleSite
    },
    {
        name:"Lorem ipsum",
        url:"/",
        image: emptyExample
    },
    {
        name:"Lorem ipsum",
        url:"/",
        image: emptyExample
    },
    {
        name:"Lorem ipsum",
        url:"/",
        image: emptyExample
    },
    {
        name:"Lorem ipsum",
        url:"/",
        image: emptyExample
    },
    {
        name:"Lorem ipsum",
        url:"/",
        image: emptyExample
    }
]

const Skills = () => {

    useLayoutEffect(() => {
        const windowWidth = window.innerWidth
        const count = windowWidth < 1024 ?  (Math.floor(windowWidth/10) * 1.5) : (Math.floor(windowWidth/10))
        randomizeZurks(count)
    });
    const zurRef = useRef()
    const SkillBars = () => {

        const elements = skillbar_data.map((item,index) => {
            return (
                    <div  key={index}   className={'skill_container item_skill_' + item.name}>
                        <div className="skill_name">{item.name}</div>
                        <div className="skill">
                            <div  className="skill_progress">
                                { item.parentSkill }

                                <span className="skill_value">{ item.exp }</span>
                            </div>
                        </div>
                    </div>
            )
        })
        return (
            elements
        )
    }

    const randomizeZurks = (count) => {

        let vWvH = (window.innerWidth/100 + window.innerHeight/100) * 3
        if (window.innerWidth <= 1024) {
            vWvH = (window.innerWidth/100 + window.innerHeight/100) * 4.5
        }
        for (let i = 0; i < count; i++) {
            if(!zurRef.current) return
            if( zurRef.current.children.length >= count ) return;
            const random = Math.floor(Math.random() * 5)
            const img =  document.createElement("img");
            img.src = zurk_data[random].img
            img.id =  Math.floor(Math.random() * count)
            if (img.id < count/2) {
                Object.entries({
                    'data-scroll': '',
                    'data-scroll-speed': '-3',
                }).forEach(([key,value]) => {
                    img.setAttribute(key,value)
                });
            }
            img.style.left = Math.random() * window.innerWidth + 'px'
            img.style.top = Math.random() * window.innerHeight/1.2 + 'px'
            img.style.opacity = Math.random() * 5
            img.style.width = Math.random() * vWvH + 'px'
            img.style.zIndex = -1;
            img.style.position = 'absolute'
            zurRef.current.append(img)
        }
    }

    return (
        <section className="skills__section">
            <div ref={zurRef} className="zurks_container"></div>
            <div className="sills_leftSide">
                    <div className="LibraryH1">
                    <h1>Library</h1>
                    </div>
                    <div className="skills_library">
                        {library_database.map(({name,url,image},index) => (
                            <div key={index}  className="skills_project">
                                <h1>{name}</h1>
                                <img onClick={(event) => {
                                    if (url.length < 5) return;
                                    window.location.href = url
                                }} src={image} alt={name}/>
                            </div>
                        ))}
                    </div>
                    <div className="coming_soon">
                        <h2> * Other projects coming soon </h2>
                    </div>
            </div>
            <div className="section_right skills_rightSide">
                <div className="SkillsH1">
                <h1>Skills</h1>
                </div>
                <div className="skill_box">
                <SkillBars />
                </div>
            </div>
        </section>
    )
}

export default Skills