import React, { useRef, useEffect, useState } from 'react'
import emailjs from '@emailjs/browser';
import './Contacts.scss'

const Contacts = () => {

    //checkItems
    const [email, setEmail] = useState('')
    const [name, setName] = useState('')
    const [emailDirty, setEmailDirty] = useState(false)
    const [nameDirty, setNameDirty] = useState(false)
    const [emailError, setEmailError] = useState('the email field cannot be empty')
    const [nameError, setNameError] = useState('the name field cannot be empty')
    const [formValid, setFormValid] = useState(false)

    const eyeRef = useRef()
    const formEl = useRef()
    useEffect(() => {

        if(emailError || nameError) {
            setFormValid(false)
        } else {
            setFormValid(true)
        }

    },[emailError,nameError])

    useEffect(() => {
        if (window.innerWidth > 768) {
            document.querySelector(".contacts__section").addEventListener("mousemove", EyeMove);
        }
        return () => {
            document.querySelector(".contacts__section").removeEventListener("mousemove", EyeMove);
        }
    }, []);


    const EyeMove = (e) => {
        // eyeRef.current.clientHeight = -e.clientY / window.innerWidth/6
        let eyeX = eyeRef.current.getBoundingClientRect().left + eyeRef.current.clientWidth / 2
        let eyeY = eyeRef.current.getBoundingClientRect().top + eyeRef.current.clientHeight / 2
        let x = e.clientX;
        let y = e.clientY;
        let rotationDegreesX = x - eyeX
        let rotationDegreesY = y - eyeY
        eyeRef.current.style.transform = "translate(" + rotationDegreesX/30 + "px," + rotationDegreesY/30 + "px)";
        // eyeRef.current.clientWidth = -e.clientX / window.innerWidth/6

        // eyeRef.current.style.cssText = `transform: translateY (${-e.clientY})`
    }

    const autoSizeArea = (element) => {

        element.target.style.height = 'calc(1vw + 1vh)'
        const heightAuto = element.target.scrollHeight;
        element.target.style.height = `${heightAuto}px`

    }

    const handleSubmit = (e) => {
        e.preventDefault()
        emailjs.sendForm('service_b3a1hk4', 'template_n1r5dw6', formEl.current, '_3sxqXITVlAQft4QU')
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });
        return false
    }

    const emailHandler = (e) => {
        setEmail(e.target.value)
        const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if ( !re.test(String(e.target.value).toLowerCase())) {
            setEmailError('wrong email')
        } else {
            setEmailError('')
        }
    }

    const nameHandler = (e) => {
        setName(e.target.value)
        if (e.target.value.length === 0) {
            setNameError('the name field cannot be empty')
        } else {
            setNameError('')
        }
    }

    const blurHandler = (e) => {
        switch (e.target.name) {
            case 'email':
                setEmailDirty(true)
                break
            case 'name':
                setNameDirty(true)
                break
        }
    }

    return (
        <section className="contacts__section">
            <div className="layers">
                <div className="layer contacts__layer__base"></div>
                <div ref={eyeRef} className="layer contacts__layer__eye"></div>
            </div>
            <div className="contacts_form">
                <div className="linear_background">
                    <div className="contacts_number_email">
                        <div className="contacts__email con_top">
                            <h3 className="contacts_h3">
                                email
                            </h3>
                            <p> dlichenko1324@gmail.com </p>
                        </div>
                        <div className="contacts__number con_top">
                            <h3 className="contacts_h3">
                                number
                            </h3>
                            <p> + 38 (091) 621 96 75 </p>
                        </div>
                    </div>
                    <div className="contacts_sendMessage">
                        <form className="send_form" ref={formEl} onSubmit={handleSubmit}>
                            <div className="user_email">
                                <h2>Email:</h2>
                                {(emailDirty && emailError) && <div className="wrong">{emailError}</div>}
                                <input onChange={emailHandler} value={email} onBlur={e => blurHandler(e)} name="email" type="email" minLength="3" placeholder="Your email"/>
                            </div>
                            <div className="user_name">
                                <h2>Name:</h2>
                                {(nameDirty && nameError) && <div className="wrong">{nameError}</div>}
                                <input onChange={nameHandler} value={name} onBlur={e => blurHandler(e)} name="name" type="text" minLength="3" placeholder="Your name"/>
                            </div>
                            <div className="user_msg">
                                <h2>Message:</h2>
                                <textarea onInput={autoSizeArea} rows="1" maxLength='200' name="message" id="send_message" cols="1"  placeholder="Hi, i need you to create... "></textarea>
                            </div>
                            <button className="send_button" type='submit' disabled={!formValid}>
                                <h2> SEND </h2>
                            </button>
                        </form>
                    </div>
                </div>
            </div>

        </section>
    )
}

export default Contacts